
import {defineComponent} from "vue"
import QrcodeVue from 'qrcode.vue'

export default defineComponent({
  name: "QRCode",

  components: {QrcodeVue},

  props: {
    show: Boolean,
    content: String,
  },

  emits: ['update:show'],

  methods: {
    close() {
      this.$emit('update:show', false)
    },
  },
})
